<template>
  <div>
    <div style="height: 500px; float: left; border: 1px solid #777777; background: #fbfbfb;">
      <div style="margin: 10px 0">Line Chart</div>
      <div style="border-bottom: 1.5px solid #f0f1f5; width: 80%; left: 10%; position: relative;"></div>
      <chart-line-js :width="500" :height="200" :chartData="chartData" :options="options"/>
    </div>
  </div>
</template>

<script>
  import ChartLineJs from "./ChartLineJs"

  export default {
    name: "ChartLine",
    components: {ChartLineJs},
    data() {
      return {
        gradient: '',
        chartData: {},
        options: {}
      }
    },
    beforeMount() {
      this.chartData = {
        labels: ['', '', '', 'a', 'b', '1-1'],
        datasets: [
          {
            backgroundColor: '#e93934',
            borderColor: '#e93934',
            borderWidth: 1.5,
//            lineTension: 0, //  

            pointRadius: 0,
//          pointBorderColor: '#fff',
//          pointBackgroundColor: '#fff',

//            pointHitRadius: 50, // point 50   .

            pointHoverRadius: 4, //hover  point 
            pointHoverBackgroundColor: '#e93934',


            data: [200, 600, 400, 200, 380, 440, 340]
          }
        ]
      }

      this.options = {
        responsive: true, //       div .      .      
                    maintainAspectRatio: false, //    ( / ) ??
        legend: {
          display: false //Hide label
        },
        tooltips: {
          //enabled: false, // tooltips X

          backgroundColor: 'white',
          yAlign: 'bottom',
          xAlign: 'left',
          yPadding: 10,
          xPadding: 0,
          bodyAlign: 'center',
          displayColors: false,
          caretSize: 0,
          bodyFontSize: 3,
          bodyFontColor: '#e93934',

          custom(tooltip) {
            if (!tooltip) return;
            // disable displaying the color box;

            //           tooltip.y = 0
            tooltip.width = 0
            tooltip.height = 0

            console.log(this)
            console.log(tooltip.body[0].lines[0]);
            console.log(tooltip)
          },

          mode: 'nearest',
          intersect: false, // false::    .
          bodyFontSize: 15,
          callbacks: {
            title: function (tooltipItem, data) {
              return //data.labels[tooltipItem[0].index];
            },
            label: function (tooltipItem, data) {
              return tooltipItem.yLabel;
            },
            footer: function (tooltipItem, data) {
              return;
            }
          }
        },
        hover: {
          mode: 'nearest',
          intersect: false,
          onHover(event, activeElements) {
            console.log(event)
            console.log(activeElements)
          }
        },
       layout:
          {
            padding: 20,
          }
        ,
        scales: {
          xAxes: [{
            gridLines: {
              display: false, //   
//              borderDash: [8, 4], //  
//              color: "#348632" //  
            }
          }],
          yAxes:
            [{
              gridLines: {
                display: true
              },
              ticks: {
//                beginAtZero:true
              }
            }]
        }
      }
    }
  }
</script>

<style scoped>

</style>



